"use client";
import { useState } from "react";
import Link from "next/link";

const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <nav className="bg-black p-4 text-white">
      <div className="container mx-auto flex justify-between items-center">
        <h1 className="text-xl font-bold">Newsletter 2.0</h1>
        <div className="hidden md:flex gap-6">
          <Link href="/">Home</Link>
          <Link href="#">About</Link>
          <Link href="#">Contact</Link>
        </div>
        <button className="md:hidden" onClick={() => setIsOpen(!isOpen)}>
          â˜°
        </button>
      </div>
      {isOpen && (
        <div className="md:hidden flex flex-col items-center mt-2 space-y-2">
          <Link href="/" onClick={() => setIsOpen(false)}>Home</Link>
          <Link href="#" onClick={() => setIsOpen(false)}>About</Link>
          <Link href="#" onClick={() => setIsOpen(false)}>Contact</Link>
        </div>
      )}
    </nav>
  );
};

export default Navbar;
